/* eslint-disable */

@font-face {
	font-family: 'S-CoreDream-Thin';
	src: url('./font/SCDream-Thin.otf') format('opentype');
}
@font-face {
	font-family: 'S-CoreDream-Light';
	src: url('./font/SCDream-Light.otf') format('opentype');
}
@font-face {
	font-family: 'S-CoreDream-Medium';
	src: url('./font/SCDream-Medium.otf') format('opentype');
}
@font-face {
	font-family: 'S-CoreDream-Bold';
	src: url('./font/SCDream-Bold.otf') format('opentype');
}
@font-face {
	font-family: 'S-CoreDream-Heavy';
	src: url('./font/SCDream-Heavy.otf') format('opentype');
}

html, body {
  padding: 0px;
  margin: 0px;
  width: 100%;
  max-width: 420px;
  /* height: 100%; */
  margin: auto;
  /* border-right: 1px solid #eee; */
  /* border-left: 1px solid #eee; */
  background: #f0f0f0;
}

body {
  margin: 0;
  font-family: 'S-CoreDream-Medium';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: white;
}

.row {
  margin-right: 0px;
  margin-left: 0px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

ul, li {
  list-style: none;
  margin: 0;
  padding: 0;
}

.footer {
  font-size: 11px;
  height: 100px;
  line-height: 24px;
  padding: 25px 0;
  background: #e3e3e3;
}

.main_img {
  width: 100%;
}

.col, .col-1, .col-10, .col-11, .col-12, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-auto, .col-lg, .col-lg-1, .col-lg-10, .col-lg-11, .col-lg-12, .col-lg-2, .col-lg-3, .col-lg-4, .col-lg-5, .col-lg-6, .col-lg-7, .col-lg-8, .col-lg-9, .col-lg-auto, .col-md, .col-md-1, .col-md-10, .col-md-11, .col-md-12, .col-md-2, .col-md-3, .col-md-4, .col-md-5, .col-md-6, .col-md-7, .col-md-8, .col-md-9, .col-md-auto, .col-sm, .col-sm-1, .col-sm-10, .col-sm-11, .col-sm-12, .col-sm-2, .col-sm-3, .col-sm-4, .col-sm-5, .col-sm-6, .col-sm-7, .col-sm-8, .col-sm-9, .col-sm-auto, .col-xl, .col-xl-1, .col-xl-10, .col-xl-11, .col-xl-12, .col-xl-2, .col-xl-3, .col-xl-4, .col-xl-5, .col-xl-6, .col-xl-7, .col-xl-8, .col-xl-9, .col-xl-auto {
  position: relative;
  width: 100%;
  padding-right: 0px;
  padding-left: 0px;
}