/* eslint-disable */

$main-color : #1e3fbe;

.item_dt_top {
    position: relative;
}

.item_dt_img {
    width: 100%;
}

.top_icon {
    position: absolute;
    width: 40px;
    height: 40px;
    top: 15px;
    text-align: center;
    line-height: 45px;
}

.back_icon {
    @extend .top_icon;
    left: 5px;
}

.share_icon {
    @extend .top_icon;
    right: 5px;
}

.item_dt_info {
    background: white;
    height: 60px;
    position: absolute;
    width: calc(100% - 26px);
    left: 13px;
    bottom: -22px;
    border-radius: 10px;
    border: 1px solid #eee;
}

.item_dt_con {
    width: 50%;
    height: 60px;
    line-height: 60px;
    font-size: 14px;
    float: left;
    padding: 0 15px;
}

.item_dt_mid {
    text-align: left;
    padding: 10px 0 50px;
}

.item_dt_select {
    border-bottom: 1px solid #eee;
    padding: 17px 13px 20px;
    position: relative;
}

.cnt_wrap {
    position: absolute;
    right: 13px;
    top: 30px;
}

.cnt_wrap div {
    float: left;
    width: 40px;
    height: 40px;
    text-align: center;
}

.cnt_minus {
    background: #f4f4f4;
    border: 1px solid #eee;
    line-height: 27px;
}

.cnt {
    border-top: 1px solid #eee;
    border-bottom: 1px solid #eee;
    line-height: 35px;
}

.cnt_plus {
    background: #f4f4f4;
    border: 1px solid #eee;
    line-height: 39px;
}

.item_dt_select .pt-3 {
    display: flex;
    line-height: 30px;
}

.select_icon {
    margin-right: 10px;
    width: 30px;
    height: 30px;
}


@mixin box-shadow( $args1, $args2 ) { 
    -webkit-box-shadow: $args1, $args2; -moz-box-shadow: $args1, $args2; box-shadow: $args1, $args2; 
} 

.box-double-shadow { 
    @include box-shadow(2px 1px 10px #999, 0px 0px 0px rgb(255, 255, 255)); 
}

.item_dt_price_div {
    display: flex;
    font-size: 18px;
    height: 57px;
    line-height: 57px;
    @extend .box-double-shadow;
}

.item_dt_btn_div {
    display: flex;
    font-size: 18px;
    height: 56px;
    line-height: 56px;
}

.item_dt_bt {
    position: sticky;
    bottom: 0;
    width: 100%;
    background: white;
}

.price_txt {
    width: 50%;
    padding-left: 20px;
}

.price {
    width: 50%;
    text-align: right;
    padding-right: 20px;
    color: $main-color;
}

.menu_btn {
    background: $main-color;
    width: 50%;
    color: white;
    text-align: center;
}

.order_btn {
    background: #272b39;
    width: 50%;
    color: white;
    text-align: center;
}