/* eslint-disable */

.App {
  text-align: center;
}

.header {
  font-family: 'S-CoreDream-Medium';
  height: 68px;
  padding-top: 30px;
  line-height: 38px;
  font-size: 20px;
  position: relative;
}

.top_wrap {
  display: flex;
  position: absolute;
  right: 0;
  top: 30px;
}

.top_btn {
  width: 38px;
  height: 38px;
  line-height: 45px;
}

.top_img {
  height: 19px;
}

.category_wrap {
  height: 58px;
  width: 100%;
  overflow-x: auto;
}

.category_wrap ul {
  height: 100%;
  width: max-content;
}

.category_wrap ul li.active {
  border-bottom: 2px solid #1E3FBE;
  font-family: 'S-CoreDream-Heavy';
  color: #333301;
}

.category_wrap li {
  height: 56px;
  border-bottom: 2px solid transparent;
  width: 98px;
  margin: 0px 3px;
  line-height: 58px;
  text-align: center;
  font-family: 'S-CoreDream-Medium';
  font-size: 14px;
  float: left;
  letter-spacing: 0.7px;
  color: #A2A2A2;
}

.li_wrap li {
  border-bottom: 2px solid #EEEEEE;
  width: calc(100% - 30px);
  margin: 0px 16px;
  height: 104px;
  position: relative;
  padding: 11px 0;
  text-align: left;
}

.item_img {
  width: 81px;
  height: 81px;
  border-radius: 10px;
}

.item_nm {
  display: inline-block;
  position: relative;
  height: 24px;
  line-height: 24px;
}

.item_nm div {
  float: left;
  color: black;
}

.item_price {
  font-family: 'S-CoreDream-Heavy';
  margin-top: 3px;
  margin-left: 2px;
  color: #2048E8;
}

.contents_wrap {
  float: left;
  padding: 13px 16px;
  letter-spacing: -0.5px;
  height: 81px;
  width: calc(100% - 109px);
  font-size: 16px;
  text-align: left;
}

.contents_wrap02 {
  float: left;
  padding: 28px 16px;
  letter-spacing: -0.5px;
  height: 81px;
  width: calc(100% - 109px);
  font-size: 16px;
  text-align: left;
}

.item_type {
  width: 40px;
  font-size: 12px;
  height: 18px;
  color: white !important;
  border-radius: 9px;
  float: right;
  margin-top: 2px;
  margin-left: 5px;
  line-height: 18px;
  text-align: center;
}

.search_inp_wrap {
  z-index: 2;
  position: absolute;
  width: 100%;
  height: 75px;
  background: white;
  top: 0;
}

.cover {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  background-color: black;
  opacity: 0.5;
  z-index: 1;
}

.search_inp {
  margin-left: 16px;
  margin-top: 35px;
  border: none;
  height: 30px;
  width: calc(100% - 88px);
  font-weight: bold;
  font-size: 15px;
  float: left;
  font-family: 'S-CoreDream-Light';
}

.search_cancel_btn {
  position: absolute;
  right: 21px;
  bottom: 7px;
  width: 40px;
  font-size: 15px;
  font-family: 'S-CoreDream-Light';
}

.search_ul {
  position: absolute;
  z-index: 2;
  background: white;
}

.item_popular {
  @extend .item_type;
  background: #fdba7a;
}

.item_signature {
  @extend .item_type;
  background: #f1087a;
}

.img_wrap {
  width: 81px;
  height: 81px;
  overflow: hidden;
  float: left;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
